.nav{
    width: 100%;
    height: 8vh;
    background-color: white ;
    position: fixed;
    top: 0;
    z-index: 1000;
    box-shadow: 0 12px 44px 0 rgba(255, 255, 255, 0.2);

}

.nav div:nth-child(1){
    margin-left: 3vw;
    margin-top: 1.5vh;
}

.nav div:nth-child(2){
    margin-right: 3vw;
}
